body {
  background-color: white;
}

.add-review-page-header {
  position: relative;
  margin: 0 auto;
  z-index: 2;
}

.main-page-header__breadcrumbs {
  margin-left: 20px;
}

.breadcrumbs {
  max-width: 700px;
}

.breadcrumbs__list {
  display: flex;
  flex-direction: row;
  align-items: center;
  flex-wrap: wrap;
}

.breadcrumbs__item {
  position: relative;
  display: inline-block;
  max-width: 500px;
  color: #ffff1b;
}

.breadcrumbs__item--my-list {
  color: #000;
}

.breadcrumbs__item--dark {
  color: #000;
}

.breadcrumbs__item:not(:last-child)::after {
  content: "/";
  margin-left: 5px;
  margin-right: 7px;
  font-size: 19px;
  line-height: 23px;
}

.breadcrumbs__link {
  display: inline-block;
  height: 19px;
  max-width: 450px;
  overflow-x: clip;
  white-space: nowrap;
  text-overflow: ellipsis;
  font-size: 19px;
  line-height: 23px;
  color: #e3e319;
  transition: transform 0.5s linear;
}

.breadcrumbs__link--dark {
  color: #000;
}

.breadcrumbs__link:hover {
  transform: scale(1.1);
}

.hero__image__add-review {
  width: 1200px;
  height: 375px;
  object-fit: cover;
}

.add-review__film-card {
  flex-direction: column;
  margin-left: auto;
  margin-right: auto;
}

.add-review__hero-image {
  position: relative;
  z-index: 2;
  min-height: 375px;
}

.add-review__additional-film-info {
  position: relative;
  margin: auto;
  transform: translateY(-170px);
  z-index: 2;
}

.additional-film-info__add-review-form {
  margin: auto;
  margin-top: 20px;
}

.add-review-form {
  width: 520px;
}

.add-review-form__rating-stars {
  margin-bottom: 15px;
}

.rating-stars {
  display: flex;
  justify-content: center;
  flex-direction: row-reverse;
}

.rating__input {
  display: none;
}

.rating__input:checked ~ .rating__label::after {
  color: rgba(56, 44, 42, 0.7);
}

.rating__input:hover ~ .rating__label::after {
  color: rgba(56, 44, 42, 0.7);
}

.rating__label {
  font-size: 0;
  line-height: 0;
  display: block;
  padding-right: 10px;
  cursor: pointer;
}

.rating__label::after {
  content: "★";
  display: block;
  font-size: 32px;
  line-height: 36px;
  color: rgba(56, 44, 42, 0.36);
  transition: color 0.2s;
}

.add-review-form___review-text {
  display: block;
}

.review-text {
  background: #f2dba8;
  border-radius: 8px;
}

.review-text__input {
  display: block;
  width: 100%;
  height: 150px;
  min-height: 150px;
  padding: 25px;
  border: none;
  background: 0 0;
  resize: vertical;
  font-size: 17px;
