  font-size: 17px;
  line-height: 20px;
  color: #866866;
  letter-spacing: 0;
  line-height: 27px;
  font-weight: 700;
  cursor: pointer;
  transition: opacity 0.5s;
  transition: 0.5s transform, 0.5s;
}

.review-text__button:hover {
  opacity: 0.5;
  transform: scale(1.2);
}

.player-page {
  position: relative;
  min-width: 1000px;
  margin: 0;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 19px;
  line-height: 23px;
  color: #ffff1b;
  background-color: #000;
}

.player-page__player {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
}

.player {
  box-sizing: border-box;
}

.player__video {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.player__exit-button {
  position: absolute;
  top: 37px;
  right: 32px;
  z-index: 2;
}

.exit-button {
  display: block;
  background: 0 0;
  border: none;
  border-radius: 8px;
  font-size: 17px;
  line-height: 20px;
  color: #ffff1b;
  padding: 10px 20px;
  width: 93px;
  cursor: pointer;
  transition: transform 0.3s linear;
}

.exit-button:hover {
  transform: scale(1.1);
}

.player__controls {
  position: absolute;
  z-index: 2;
  left: 0;
  right: 0;
  bottom: 0;
}

.controls {
  padding: 25px;
}

.controls__row {
  display: flex;
  flex-direction: row;
  align-items: center;
  margin-bottom: 15px;
}

.controls__progress {
  margin-right: 20px;
}

.progress {
  position: relative;
  flex-grow: 1;
}

.progress__line {
  display: block;
  width: 100%;
  background: rgba(255, 251, 231, 0.35);
  border-radius: 1px;
  border: none;
  height: 4px;
}

.progress__toggler {
  display: block;
  position: absolute;
  left: 0;
  top: 50%;
  width: 17px;
  height: 17px;
  border-radius: 50%;
  background: #d9cd8d;
  font-size: 0;
  transform: translate(-50%, -50%);
  cursor: pointer;
}

.controls__time-value {
  flex-shrink: 0;
  width: 60px;
  text-align: right;
  color: #ffff1b;
  font-size: 14px;
}

.controls__play-button {
  display: block;
  background: 0 0;
  border: none;
  width: 26px;
  height: 26px;
  font-size: 0;
  padding: 0;
  transition: 0.5s transform ease;
  cursor: pointer;
}

.controls__play-button:hover {
  transform: scale(1.1);
}

.controls__play-icon {
  fill: #FFF9D9;
  transition: fill 0.3s ease;
}

.controls__play-icon:hover {
  fill: #ffff1b;
}

.controls__name {
  color: #ffff1b;
  font-size: 17px;
  line-height: 20px;
  font-weight: 700;
  margin: 0 30px;
}

.controls__full-screen-button {
  margin-left: auto;
}

.full-screen-button svg {
  vertical-align: top;
}

.full-screen-button {
  display: block;
  font-size: 0;
  background: 0 0;
  border: none;
  width: 27px;
  height: 27px;
  padding: 0;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.full-screen-button:hover {
  transform: scale(1.1);
}

.full-screen-button__icon {
  fill: #FFF9D9;
  transition: fill 0.3s ease;
}

.full-screen-button__icon:hover {
  fill: #ffff1b;
}

.sign-in__title {
  position: relative;
  margin: 0 auto;
}

.title {
  font-size: 32px;
  line-height: 45px;
  color: #ffff1b;
  text-shadow: -1px 0 black, 0 1px black, 1px 0 black, 0 -1px black;
  text-align: center;
  max-width: 300px;
}

.sign-in__form-container {
  position: relative;
  margin: auto;
  text-align: center;
  transform: translateY(-130px);
  max-width: 540px;
  width: 100%;
  z-index: 2;
}

.sign-in__form-container {
  margin-top: -50px;
}

.form-container {
  background: url("../../../src/images/script-scroll.jpg") 32px 246px repeat-y;
  min-height: 400px;
}

.form-container--bottom {
  min-height: 440px; /* Увеличиваем высоту контейнера на 40 пикселей */
  background-image: url("../../../src/images/script-bottom-without-bg.png");
  background-position: 6px calc(100% + 32px);
  background-repeat: no-repeat;
}

.form-container--top {
  min-height: 440px;
  background: url("../../../src/images/script-top-without-bg.png") left -40px no-repeat;
}

.sign-in__form {
  padding: 60px 50px 50px 60px;
}

.sign-in {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
}

.sign-in__message-text {
  margin-bottom: 30px;
}

.message-text {
  font-size: 19px;
  line-height: 23px;
  text-align: start;
}

.message-text--normal {
  color: #000;
}

.message-text--error {
  color: #ff0000;
}

.sign-in__fields {
  margin-bottom: 30px;
}

.sign-in__field {
  margin-bottom: 3px;
  position: relative;
}

.sign-in__input {
  position: relative;
  display: block;
  width: 100%;
  background: 0 0;
  border-radius: 8px;
  border: 1px solid transparent;
  outline: 0;
  color: #000;
  font-size: 32px;
  line-height: 45px;
  padding: 25px 20px;
  transition: border-color 0.5s;
}

.sign-in__input:hover {
  border-color: #ffff1b;
}

.sign-in__input:focus {
  border-color: #ffff1b;
}

.sign-in__button {
  margin-bottom: 30px;
}